<template>
    <div>
        <h3>Cantidad de juegos con stock: {{ getGamesInStock }}</h3>
        <div v-for="(item, index) in getGamesList" v-bind:key="index">  
            <div class="gamesBox">
                <h4>{{ item.id }}</h4>
                <h4>{{ item.nombre }}</h4>
                <h4>Stock: {{ item.stock }}</h4>
                <h4>$ {{ item.precio }}</h4>
                <input type="button" value="Vender" @click="sellGame(item)">
            </div>
        </div>
    </div>
</template>
<script>
import { mapActions } from 'vuex'

export default 
{ 
    computed: 
    {
        getGamesList() 
        { 
            return this.$store.getters.getterGamesList
        },
        getGamesInStock() 
        { 
            return this.$store.getters.getterGamesInStock
        }
    },
    methods:
    {
        ...mapActions(['sellGame'])
    }
}
</script>
<style scoped>
    .gamesBox
    { 
        display: flex;
        flex-direction: row; 
    }
    h3
    {
        color: green;
        padding: 8px;
    }
    h4
    {
        width: 192px;
        padding: 8px;
    }
    input
    {
        margin-left: -96px;
        transform:translateY(-6px) ;
    }
</style>
